import { Component, Prop, Event, EventEmitter, h } from '@stencil/core';

export type ContainerFooterSize = 'minimal' | 'standard';

@Component({
  tag: 'bh-container-footer',
  styleUrl: 'bh-container-footer.css',
  shadow: true,
})
export class BhContainerFooter {
  /**
   * Size variant of the footer
   */
  @Prop() size: ContainerFooterSize = 'standard';

  /**
   * Label for the footer button
   */
  @Prop() buttonLabel: string = 'View report';

  /**
   * Event emitted when the button is clicked
   */
  @Event() bhButtonClick!: EventEmitter<void>;

  private handleButtonClick = () => {
    this.bhButtonClick.emit();
  };

  render() {
    const footerClasses = {
      'container-footer': true,
      [`container-footer-${this.size}`]: true,
    };

    return (
      <div class={footerClasses}>
        <div class="container-footer-content">
          <bh-button
            hierarchy="quaternary"
            
            size="sm"
            label={this.buttonLabel}
            onClick={this.handleButtonClick}
          />
        </div>
      </div>
    );
  }
}
